@import url("https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@300;800&display=swap");

.App {
  text-align: center;
  font-family: "Yanone Kaffeesatz", sans-serif;
}

.App-logo {
  height: 18vmin;
  pointer-events: none;
  border-radius: 3vmin;
}

@media (prefers-reduced-motion: no-preference) {
}

.App-header {
  background-image: url("fwbg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

a {
  color: #4c4c4c;
  text-decoration: none;
}

a:hover {
  color: #ff6802;

  -o-transition: 1s;
  -ms-transition: 1s;
  -moz-transition: 1s;
  -webkit-transition: 1s;
  /* ...and now for the proper property */
  transition: 1s;
}

h2 {
  margin-bottom: 0;
}
p {
  margin-top: 0;
}
